

export default {
    actions:{
       async weatherFetch({commit},position = 'kiev'){
            let dataWeather = await fetch(`https://api.weatherapi.com/v1/forecast.json?key=69f5dcd0f23c44a997743916220904&q=${position}&days=3&aqi=yes&alerts=yes`);
                dataWeather = await dataWeather.json();
                console.log(dataWeather);
                commit('updateWeather',dataWeather);
       }
    },
    mutations:{
        updateWeather(state,dataWeather){
            state.weather = dataWeather;
            //console.log('mutations Weather',state.weather);
        }
    },
    state:{
       weather:[]
    },
    getters:{
        allWeather(state){
            return state.weather
        },
        showWeaterTemp(state){
            for(let item in state.weather.current){
                    if (item == 'feelslike_c') {
                        return state.weather.current[item]
                }
                
            }
        },
        showWeaterImg(state){
            for(let item in state.weather.current){
                if (item == 'condition') {
                   return state.weather.current[item].icon
                }
            }
        },
        showHoursWeather(state){
            let arrHours= [];
            let nowDate = new Date;
            
            for(let item in state.weather.forecast){
              if (item == 'forecastday') {
                for (let i = 0; i < 3; i++) {
                  arrHours.push(state.weather.forecast[item][i].hour)
                }
                arrHours = [...arrHours[0],...arrHours[1],...arrHours[2]]
                //console.log(arrHours);
                
                let result = arrHours.filter(item => item.time_epoch >= (nowDate.getTime())/1000);
                   result = result.filter((item,i) => i % 2 == 0 );
                
                  return result.slice(0, 5)
              }
            }
           
      
          },
        showNameWeather(state){
          for(let item in state.weather.location){
            if(item == 'name'){
              return state.weather.location[item] + ',' + state.weather.location['region']
            }
          }
        },
        showDaysWeather(state){
          let arr= [];
          for(let item in state.weather.forecast){
            if (item == 'forecastday') {
              for (let i = 0; i < 3; i++) {
                arr.push(state.weather.forecast[item][i].day)
              }
              console.log('showDaysWeather',arr);
             return arr
          }
        }
      },
      showDayWeek(){
        let arr = [];
        let date = new Date();
        
        date.setDate(date.getDate());
        arr.push(date.toLocaleString("en",{weekday: 'long',}))
        
        for (let i = 1; i <= 2; i++) {
          date.setDate(date.getDate() + 1);
          arr.push(date.toLocaleString("en",{weekday: 'long',}))
        }
        return arr;
      },
    }
}