

export default {
  actions: {
    async fetchNews({ commit }) {
      const data = await fetch("/assets/json/requst.json");
      const news = await data.json();
      
      commit("getDateAgo", news);
      //commit("commitId");
      
    },
  },
  mutations: {
    getDateAgo(state, news) {
      if (news.articles) {
        let output = news.articles;
        let dateNow = new Date();
        output = output.map((item) => {
          let dateAgo = Date.parse(item.publishedAt);
          if (dateAgo / 1000 / 60 > 60) {
            let result =
              ((dateNow - dateAgo) / 1000 / 60 - dateNow.getTimezoneOffset()) /
              60;
            if (result < 1) {
              result = Math.round(result * 60) + "m ago";
            }
            if (result > 1) {
              result = Math.round(result) + "h ago";
            }
            return (item = { ...item, dateAgo: result });
          }
        });
        //console.log("output", output);
        return (state.news = output);
      }
      return (state.news = news);
    },
    // commitId(state){
    //   console.log('result mutate');
    //     let result = state.news.map( item=>{
    //     let output = ({...item, id: item.source.name.split(' ').join('-')+ `${Math.round(Math.random() *3112) }`});
        

    //     return output
        
    //   })
    //   //console.log('result mutate',result);
    //   return state.news = result
    // },
    updateTargetNews(state,news){
        state.targetNews = news
        //console.log(state.targetNews);
    },
    updateSearсh(state,searthText){
      console.log(searthText);
      if(searthText){
        return state.newsOutput = state.news.filter(i=> `${i.title}`
                                    .toLowerCase()
                                    .includes(searthText));
      }
      return state.news
    },
    updateActiveClass(state,boolean){
      state.activeClass = boolean;
    }
    
  },
  state: {
    news: [],
    newsOutput:[],
    targetNews:[],
    activeClass: ''
  },
  getters: {
    allNews(state) {
      if (state.newsOutput.length >=1) {
        return state.newsOutput
      }
      return state.news;
    },
    targetNews(state){
      return state.targetNews;
    },
    showClass(state){
      return state.activeClass
    }
  },
};