<template>
    
        <nav >
            <div 
                :style="{'display':statusBar}"
                class="overlay"
                :class="{'slideOutRight':!clickNavBar,
                        'slideInLeft': clickNavBar}"
                    >
                <div class="d-flex flex-column align-items-center">
                    <div class="p-4"
                    @click="clickRoute"
                    >
                        <router-link to="/"><h1>Home</h1></router-link>
                    </div>
                    <div class="p-4"
                    @click="clickRoute"
                    >
                        <router-link to="/weather"><h1>Weather</h1></router-link>
                        
                    </div>
                    <!-- <div class="p-4"
                    @click="clickRoute"
                    >
                        <router-link to="/stock"><h1>Stocks</h1></router-link>
                        
                    </div>
                    <div class="p-4">
                        <h1>Politic</h1>
                    </div>
                    <div class="p-4">
                        <h1>World</h1>
                    </div> -->
                    
                </div>
                
            </div>
            <div 
            class="iconBar text-primary"
            @click="clickIconNav"
            >
            <i 
            class="fa-solid fa-xl"
            :class="{'fa-bars': !clickNavBar,
                    'fa-bars-staggered':clickNavBar,
                    'active':clickNavBar}">
            </i>
           </div>
        </nav>
    
</template>

<script>
export default {
    name:'TheNavigation',
    data(){
        return{
            clickNavBar: false,
            statusBar: 'none'
            
            
        }
    },
    methods:{
        clickIconNav(){
            this.clickNavBar = !this.clickNavBar; 
            this.statusBar = 'block'
        },
        clickRoute(){
            this.statusBar = 'none';
            this.clickNavBar = false;
        }
        
    }
}
</script>

<style scoped>
nav a {
    text-decoration: none;
    color: #000000;
}
.router-link-active{
    color: #ff1493;
    
}
.router-link-active h1{
    border-bottom: 2px solid deeppink;
    
}
.iconBar{
    position: fixed;
    bottom: 0;
    right: 0;
    padding: 20px;
    margin: 10px;
    background: #B7B7B7;
    border: #B7B7B7;
    border-radius: 50%;
    transition: 0.3s ease-in-out;
    z-index: 21; 
    
    
}
.overlay {
  position: fixed; /* Sit on top of the page content */
  display: block; /* Hidden by default */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #e9e9e9; /* Black background with opacity */
  z-index: 20; /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer; /* Add a pointer on hover */
  
}

.slideInLeft {
  -webkit-animation-name: slideInLeft;
  animation-name: slideInLeft;
  -webkit-animation-duration: 0.2s;
  animation-duration: 0.2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  }
  @-webkit-keyframes slideInLeft {
  0% {
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
  visibility: visible;
  }
  100% {
  -webkit-transform: translateX(0);
  transform: translateX(0);
  }
  }
  @keyframes slideInLeft {
  0% {
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
  visibility: visible;
  }
  100% {
  -webkit-transform: translateX(0);
  transform: translateX(0);
  }
  } 


   .slideOutRight {
  -webkit-animation-name: slideOutRight;
  animation-name: slideOutRight;
  -webkit-animation-duration: 0.2s;
  animation-duration: 0.2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  }
  @-webkit-keyframes slideOutRight {
  0% {
  -webkit-transform: translateX(0);
  transform: translateX(0);
  }
  100% {
  visibility: hidden;
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
  }
  }
  @keyframes slideOutRight {
  0% {
  -webkit-transform: translateX(0);
  transform: translateX(0);
  }
  100% {
  visibility: hidden;
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
  }
  } 
@media(min-width: 1024px){
    .iconBar{
        display: none;
    }
    
}

</style>